import { faTwitter } from "@fortawesome/free-brands-svg-icons/faTwitter";
import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Badge, Col } from "react-bootstrap";
import { useQuery } from "react-query";
import MediaQuery from "react-responsive";
import { withStyles } from "@material-ui/styles";
import { Link, NavLink } from "react-router-dom";
import { useAuthUser } from "../context/authContext";
import { getNotifications } from "../utils/apiClient";
import { headerList } from "../utils/OptionsList";
import styles from "../styles/HeaderStyle";

const Header = ({ classes }) => {
  const { currentUser } = useAuthUser();
  const { data: notifications } = useQuery(
    ["Notifications", currentUser.token],
    getNotifications
  );
  const notificationsCount = notifications
    ? notifications.filter((n) => !n.read).length
    : 0;

  console.log("header");
  return (
    <Col className={classes.root}>
      <div className="m-2 mr-xl-auto ml-xl-4">
        <Link
          className={`${classes.brand} btn text-primary btn-naked-primary rounded-circle`}
          to="/home"
        >
          <FontAwesomeIcon size="2x" icon={faTwitter} />
        </Link>
      </div>
      <div>
        {headerList.map((item) => {
          const badge = item.count ? (
            <>
              {" "}
              <Badge
                className={`${classes.badge} text-primary badge bg-white"  variant="light"`}
              >
                {notificationsCount > 0 ? notificationsCount : ""}
              </Badge>
              <span className="sr-only">new items</span>
            </>
          ) : null;
          return (
            <div
              key={item.name}
              className="d-flex align-items-top position-relative"
            >
              {badge}
              <NavLink
                to={
                  item.name === "Profile"
                    ? `${item.href}/${currentUser?.user?._id}`
                    : item.href
                }
                className={`${classes.hedarLinks}  p-1 mx-lg-1 py-xl-2 mb-1 btn-lg btn-secondary rounded-pill `}
                activeClassName="active"
              >
                <FontAwesomeIcon className="m-2" size="lg" icon={item.icon} />
                <span className="d-none d-xl-block my-3">{item.name}</span>
              </NavLink>
            </div>
          );
        })}
      </div>
      <Link
        class={`${classes.postBtn} btn btn-primary rounded-pill`}
        id="compose"
        to="/compose/post"
      >
        <span className="d-none d-xl-block mx-auto px-2">Post</span>
        <MediaQuery maxWidth={1199}>
          <svg viewBox="0 0 24 24">
            <path d="M8.8 7.2H5.6V3.9c0-.4-.3-.8-.8-.8s-.7.4-.7.8v3.3H.8c-.4 0-.8.3-.8.8s.3.8.8.8h3.3v3.3c0 .4.3.8.8.8s.8-.3.8-.8V8.7H9c.4 0 .8-.3.8-.8s-.5-.7-1-.7zm15-4.9v-.1h-.1c-.1 0-9.2 1.2-14.4 11.7-3.8 7.6-3.6 9.9-3.3 9.9.3.1 3.4-6.5 6.7-9.2 5.2-1.1 6.6-3.6 6.6-3.6s-1.5.2-2.1.2c-.8 0-1.4-.2-1.7-.3 1.3-1.2 2.4-1.5 3.5-1.7.9-.2 1.8-.4 3-1.2 2.2-1.6 1.9-5.5 1.8-5.7z" />
          </svg>
        </MediaQuery>
      </Link>
    </Col>
  );
};
export default withStyles(styles)(Header);
